import React, { useState } from "react";
import HeaderLS from "./HeaderLS";
import GameLS from "./GameLS";
import PlayLS from "./PlayLS";
import FooterLS from "./FooterLS";

import { BrowserRouter as Router } from 'react-router-dom'

const BonusGame = () => {
  const [score, setScore] = useState(0);

  const [monChoix, setMonChoix] = useState("");

  const [show, setShow] = useState(false);

  const handleChange = () => {
    setShow(!show);
  };

  return (
    <Router>
      <>
        <div className="container">
          <HeaderLS score={score} />

          {show ? (
            <GameLS
              monChoix={monChoix}
              score={score}
              setScore={setScore}
              handleChange={handleChange}
            />
          ) : (
            <PlayLS setMonChoix={setMonChoix} handleChange={handleChange} />
          )}
        </div>
        <FooterLS />
      </>
    </Router>
  );
};

export default BonusGame;
