import React from "react";
import styled from "styled-components";

const PlayContainer = styled.section`
  width: 90%;
  position: relative;
  margin: 80px auto 0 auto;
  display: flex;
  flex-direction: column;
  @media (min-width: 768px) {
    margin: 4.5rem auto 0 auto;
  }
  @media (min-height: 1024px) {
    position: absolute;
    top: 35%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  @media (max-height: 668px) {
    margin: 1.5rem auto 0 auto;
  }
`;

const TriangleBg = styled.img`
  position: absolute;
  align-self: center;
  margin-top: 4.375rem;
  width: 250px;
  @media (min-width: 768px) {
    width: 400px;
  }
  @media (max-height: 668px) {
    width: 200px;
  }
`;

const OptionsContainer = styled.div`
  display: flex;
  justify-content: center;
`;

const DivPaperClassic = styled.div`
  transform: translateY(2rem);
  cursor: pointer;
  @media (min-width: 768px) {
    transform: translateY(2rem);
  }
  @media (max-height: 668px) {
    transform: translateX(1rem) translateY(2rem);
  }
`;

const DivScissorsClassic = styled.div`
  transform: translateX(6rem) translateY(2rem);
  cursor: pointer;
  @media (min-width: 768px) {
    transform: translateX(8em) translateY(2rem);
  }
  @media (max-height: 668px) {
    transform: translateX(4.5rem) translateY(2rem);
  }
`;

const DivRockClassic = styled.div`
  transform: translateX(-6.5rem) translateY(12.5rem);
  cursor: pointer;
  @media (min-width: 768px) {
    transform: translateX(-9rem) translateY(18.5rem);
  }
  @media (max-height: 668px) {
    transform: translateX(-5.75rem) translateY(11rem);
  }
`;

const Options = styled.div`
  transition: transform 0.2s;
`;

const PlayClassic = ({ handleChange, setMonChoix }) => {
  const setChoix = (e) => {
    setMonChoix(e.target.dataset.id);
  };

  return (
    <PlayContainer>
      <TriangleBg
        src={process.env.PUBLIC_URL + "../images/bg-triangle.svg"}
        alt="triangle"
      />
      <OptionsContainer onClick={handleChange}>
        <DivPaperClassic>
          <Options
            data-id="paper"
            onClick={setChoix}
            className="paper"
            id="paper"
          />
        </DivPaperClassic>
        <DivScissorsClassic>
          <Options
            data-id="scissors"
            onClick={setChoix}
            className="scissors"
            id="scissors"
          />
        </DivScissorsClassic>
        <DivRockClassic>
          <Options
            data-id="rock"
            onClick={setChoix}
            className="rock"
            id="rock"
          />
        </DivRockClassic>
      </OptionsContainer>
    </PlayContainer>
  );
};

export default PlayClassic;
