import React, { useState } from "react";
import styled from "styled-components";

const FooterContainer = styled.footer`
  width: 100vw;
`;

const Copyright = styled.div`
  position: absolute;
  width: 100%;
  left: 50%;
  transform: translate(-50%);
  bottom: 1rem;
  font-size: 0.7rem;
  color: white;

  a {
    font-size: 0.7rem;
    color: hsl(39, 89%, 49%);
    text-decoration: none;
  }
  a:hover {
    color: hsl(349, 71%, 52%);
  }
`;

const RulesBtn = styled.button`
  position: absolute;
  bottom: 3rem;
  right: 1rem;
  padding: 10px 25px;
  border: 2px solid hsl(217, 16%, 45%);
  border-radius: 0.4rem;
  color: hsl(217, 16%, 45%);
  background: none;
  font-weight: 600;
  letter-spacing: 2px;
  text-transform: uppercase;

  &:hover {
    border: 3px solid white;
    background: hsl(230, 89%, 62%);
    color: white;
    cursor: pointer;
    transition: -webkit-transform 500ms ease-in-out;
    transition: transform 500ms ease-in-out;
    transition: transform 500ms ease-in-out, -webkit-transform 500ms ease-in-out;
  }
  @media(min-width: 768px) {
    right: 2rem;
  }
`;

// POP UP WITH RULES
const RulesHeader = styled.section`
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  z-index: 10;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: hsl(229, 25%, 31%);
  background-color: #fff;
  border-radius: 8px;
  padding: 30px;
  transition: 0.3s ease-in-out;

  @media (min-width: 768px) {
    width: 400px;
    height: 415px;
  }
`;

const RulesWrap = styled.article`
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;

  h1 {
    text-transform: uppercase;
    margin: 0;
    padding: 0;
    grid-raw: 1;
  }
  @media (max-width: 768px) {
    h1 {
      font-size: 2.1rem;
      position: absolute;
      top: 10%;
      left: 50%;
      transform: translate(-50%, 0);
    }
  }
`;

const CloseBtn = styled.button`
  background: none;
  border: none;
  align-self: center;
  color: hsl(229, 25%, 31%);
  cursor: pointer;
  transition: 0.3s ease-in-out;

  img:hover {
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg);
    -webkit-transition: -webkit-transform 500ms ease-in-out;
    transition: -webkit-transform 500ms ease-in-out;
    transition: transform 500ms ease-in-out;
    transition: transform 500ms ease-in-out, -webkit-transform 500ms ease-in-out;
  }

  @media (max-width: 768px) {
    position: absolute;
    bottom: 7%;
    left: 50%;
    transform: translate(-50%, 0);
  }
`;

const RulesImg = styled.img`
  width: 100%;
  max-width: 400px;
`;

const FooterClassic = () => {
  const [show, setShow] = useState(false);

  let urlFrontend = "https://www.frontendmentor.io?ref=challenge";
  let urlRlpz = "https://github.com/Zepolimer?tab=repositories";

  return (
    <FooterContainer>
      <Copyright class="attribution">
        Challenge by{" "}
        <a href={urlFrontend} target="_blank" rel="noreferrer">
          Frontend Mentor
        </a>
        . Coded by <a href={urlRlpz}>Remi Lopez</a>.
      </Copyright>
      <RulesBtn onClick={() => setShow(true)}>Rules</RulesBtn>
      {show ? (
        <RulesHeader>
          <RulesWrap>
            <h1>Rules</h1>
            <CloseBtn onClick={() => setShow(false)}>
              <img src={process.env.PUBLIC_URL + "../images/icon-close.svg"} alt="Close" />
            </CloseBtn>
          </RulesWrap>
          <RulesImg src={process.env.PUBLIC_URL + "../images/image-rules.svg"} alt="Rules" />
        </RulesHeader>
      ) : null}
    </FooterContainer>
  );
};

export default FooterClassic;
