import React, { useState } from "react";
import styled from "styled-components";
import BonusGame from "./BonusVersion/BonusGame";
import ClassicGame from "./ClassicVersion/ClassicGame";

import { BrowserRouter as Router } from 'react-router-dom'

const Complex = styled.button`
  position: absolute;
  bottom: 3rem;
  left: 1rem;
  padding: 10px 10px;
  border: 2px solid hsl(217, 16%, 45%);
  border-radius: 0.4rem;
  color: hsl(217, 16%, 45%);
  background: none;
  font-weight: 600;
  letter-spacing: 2px;
  text-transform: uppercase;

  &:hover {
    border: 3px solid white;
    background: hsl(230, 89%, 62%);
    color: white;
    cursor: pointer;
    transition: -webkit-transform 500ms ease-in-out;
    transition: transform 500ms ease-in-out;
    transition: transform 500ms ease-in-out, -webkit-transform 500ms ease-in-out;
  }
  @media(min-width: 768px) {
    left: 2rem;
  }
`;

const SwitchBtn = () => {
  const [showClassic, setShowClassic] = useState(true);

  return (
    <Router>
      {showClassic && <BonusGame onClick={showClassic} />}
      {!showClassic && <ClassicGame />}
      <Complex onClick={() => setShowClassic(!showClassic)}>Switch</Complex>
    </Router>
  );
};

export default SwitchBtn;
