import React, { useState } from "react";
import HeaderClassic from './HeaderC';
import PlayClassic from './PlayC';
import GameClassic from "./GameC";
import FooterClassic from "./FooterC";

import { BrowserRouter as Router } from 'react-router-dom'

const ClassicGame = () => {
  const [score, setScore] = useState(0);

  const [monChoix, setMonChoix] = useState("");

  const [show, setShow] = useState(false);

  const handleChange = () => {
    setShow(!show);
  };

  return (
    <Router>
      <>
        <div className="container">
          <HeaderClassic score={score} />

          {show ? (
            <GameClassic
              monChoix={monChoix}
              score={score}
              setScore={setScore}
              handleChange={handleChange}
            />
          ) : (
            <PlayClassic
              setMonChoix={setMonChoix}
              handleChange={handleChange}
            />
          )}
        </div>
        <FooterClassic />
      </>
    </Router>
  );
};

export default ClassicGame;
