import React, { useEffect, useState } from "react";
import styled from "styled-components";

const GameContainer = styled.section`
  width: 95%;
  height: 70%;
  margin: 80px auto 0 auto;
  display: grid;
  grid-template-columns: 50% 50%;
  grid-row: 50% 50%;
  align-items: center;
  justify-content: center;

  @media (min-width: 769px) {
    width: 80%;
    position: absolute;
    top: 30%;
    left: 50%;
    transform: translate(-50%, -30%);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  @media (max-height: 668px) {
    margin: 50px auto 0 auto;
  }
  
`;

const JoueurChoix = styled.div`
  grid-row: 1;
  grid-column: 1;
  display: flex;
  flex-direction: column-reverse;
  cursor: pointer;

  @media (min-width: 769px) {
    display: flex;
    flex-direction: column;
  }
`;

const SpanText = styled.span`
  color: white;
  text-transform: uppercase;

  @media (min-width: 768px) {
    text-transform: uppercase;
    font-size: 1.2rem;
    color: white;
    z-index: 10;
    margin-bottom: 50px;
  }
  @media (max-height: 668px) {
    font-size: 1rem;
  }
`;

const ResultContainer = styled.div`
  grid-row: 2;
  grid-column: 1/3;
  display: flex;
  flex-direction: column;
  margin: 10% 0 0 0;
  align-items: center;

  @media (min-width: 768px) {
    display: flex;
    flex-direction: column;
    margin: 0 6%;
  }
`;

const SpanResult = styled.span`
  color: white;
  text-transform: uppercase;
  font-size: 3rem;
  font-weight: 700;
  margin-bottom: 10px;

  @media (min-width: 768px) {
    font-size: 3.4rem;
  }
  @media (max-height: 668px) {
    margin-top: 20px;
    font-size: 2rem;
  }
`;

const PlayAgain = styled.div`
  width: 220px;
  background-color: white;
  text-decoration: none;
  text-transform: uppercase;
  padding: 10px 20px;
  border-radius: 5px;
  font-size: 1rem;
  color: hsl(237, 49%, 15%);
  cursor: pointer;

  &:hover {
  background-color: transparent;
  color: white;
  border: 2px dashed white;
  }

  @media (min-width: 768px) {
    background-color: white;
    text-decoration: none;
    text-transform: uppercase;
    padding: 10px 20px;
    border-radius: 5px;
    font-size: 0.8rem;
    width: 15.277777777777779vw;
  }
  @media (max-height: 668px) {
    width: 160px;
    padding: 8px 15px;
  }
`

const RobotChoix = styled.div`
  grid-row: 1;
  grid-column: 2;
  display: flex;
  flex-direction: column-reverse;
  cursor: pointer;

  @media (min-width: 769px) {
    display: flex;
    flex-direction: column;
  }
`;

const GameLS = ({ score, monChoix, setScore, handleChange }) => {
  const [robot, setRobot] = useState("");
  const [joueurGagne, setJoueurGagne] = useState("");

  const newRobotJoue = () => {
    const choix = ["rock", "paper", "scissors", "lizard", "spock"];
    setRobot(choix[Math.floor(Math.random() * 5)]);
  };

  useEffect(() => {
    newRobotJoue();
  }, [setRobot]);

  const Result = () => {
    if ((monChoix === "rock" && robot === "scissors") || (monChoix === "rock" && robot === "lizard")) {
      setJoueurGagne("win");
      setScore(score + 1);
    } else if ((monChoix === "rock" && robot === "paper") || (monChoix === "rock" && robot === "spock")) {
      setJoueurGagne("lose");
      setScore(score - 1);
    } else if ((monChoix === "paper" && robot === "rock") || (monChoix === "paper" && robot === "spock")) {
      setJoueurGagne("win");
      setScore(score + 1);
    } else if ((monChoix === "paper" && robot === "scissors") || (monChoix === "paper" && robot === "lizard")) {
      setJoueurGagne("lose");
      setScore(score - 1);
    } else if ((monChoix === "scissors" && robot === "paper") || (monChoix === "scissors" && robot === "lizard")) {
      setJoueurGagne("win");
      setScore(score + 1);
    } else if ((monChoix === "scissors" && robot === "rock") || (monChoix === "scissors" && robot === "spock")) {
      setJoueurGagne("lose");
      setScore(score - 1);
    } else if ((monChoix === "lizard" && robot === "spock") || (monChoix === "lizard" && robot === "paper")) {
      setJoueurGagne("win");
      setScore(score + 1);
    } else if ((monChoix === "lizard" && robot === "rock") || (monChoix === "lizard" && robot === "scissors")) {
      setJoueurGagne("lose");
      setScore(score - 1);
    } else if ((monChoix === "spock" && robot === "scissors") || (monChoix === "spock" && robot === "rock")) {
      setJoueurGagne("win");
      setScore(score + 1);
    } else if ((monChoix === "spock" && robot === "lizard") || (monChoix === "spock" && robot === "paper")) {
      setJoueurGagne("lose");
      setScore(score - 1);
    } else {
      setJoueurGagne("draw");
    }
  };

  useEffect(() => {
    Result();
  }, [robot]);

  return (
    <GameContainer>
      <JoueurChoix>
        <SpanText>You picked</SpanText>
        <div
          className={`icon ${monChoix} ${
            joueurGagne === "win" ? `icon ${monChoix} winner` : ""
          }`}
        ></div>
      </JoueurChoix>
      {joueurGagne === "win" && (
        <ResultContainer>
          <SpanResult>You Win</SpanResult>
          <PlayAgain onClick={() => setRobot()} onClick={handleChange}>
            Play Again
          </PlayAgain>
        </ResultContainer>
      )}
      {joueurGagne === "lose" && (
        <ResultContainer>
          <SpanResult>You Lose</SpanResult>
          <PlayAgain onClick={() => setRobot()} onClick={handleChange}>
            Play Again
          </PlayAgain>
        </ResultContainer>
      )}
      {joueurGagne === "draw" && (
        <ResultContainer>
          <SpanResult>Draw</SpanResult>
          <PlayAgain onClick={() => setRobot()} onClick={handleChange}>
            Play Again
          </PlayAgain>
        </ResultContainer>
      )}

      <RobotChoix>
        <SpanText>The House Picked</SpanText>
        <div
          className={`icon ${robot} ${
            joueurGagne === "lose" ? `icon ${robot} winner` : ""
          }`}
        ></div>
      </RobotChoix>
    </GameContainer>
  );
};

export default GameLS;
