import React from "react";
import styled from "styled-components";

const PlayContainer = styled.section`
  width: 90%;
  position: relative;
  margin: 80px auto 0 auto;
  display: flex;
  flex-direction: column;
  @media (min-width: 768px) {
    margin: 2rem auto 0 auto;
  }
  @media (min-height: 1024px) {
    position: absolute;
    top: 35%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  @media (max-height: 668px) {
    margin: 1.5rem auto 0 auto;
  }
`;

const TriangleBg = styled.img`
  position: absolute;
  align-self: center;
  margin-top: 4.375rem;
  width: 220px;
  @media (min-width: 768px) {
    width: 400px;
  }
`;

const OptionsContainer = styled.div`
  display: flex;
  justify-content: center;
`;

const DivPaper = styled.div`
  transform: translateX(12.5rem) translateY(6.5rem);
  cursor: pointer;
  @media (min-width: 768px) {
    transform: translateX(20rem) translateY(9rem);
  }
  @media (max-height: 668px) {
    transform: translateX(12rem) translateY(6.5rem);
  }
`;

const DivScissors = styled.div`
  transform: translateX(12rem) translateY(1rem);
  cursor: pointer;
  @media (min-width: 768px) {
    transform: translateX(18.25em) translateY(1.25rem);
  }
  @media (max-height: 668px) {
    transform: translateX(11.25rem) translateY(1.5rem);
  }
`;

const DivRock = styled.div`
  transform: translateX(-8rem) translateY(15rem);
  cursor: pointer;
  @media (min-width: 768px) {
    transform: translateX(-10.5rem) translateY(22.25rem);
  }
  @media (max-height: 668px) {
    transform: translateX(-7.5rem) translateY(14rem);
  }
`;

const DivLizard = styled.div`
  transform: translateX(-5rem) translateY(15rem);
  cursor: pointer;
  @media (min-width: 768px) {
    transform: translateX(-8rem) translateY(22.25rem);
  }
  @media (max-height: 668px) {
    transform: translateX(-4rem) translateY(14rem);
  }
`;

const DivSpock = styled.div`
  transform: translateX(-13rem) translateY(6.5rem);
  cursor: pointer;
  @media (min-width: 768px) {
    transform: translateX(-20rem) translateY(9rem);
  }
  @media (max-height: 668px) {
    transform: translateX(-12rem) translateY(6.5rem);
  }
`;

const Options = styled.div`
  transition: transform 0.2s;
`;

const PlayLS = ({ handleChange, setMonChoix }) => {
  const setChoix = (e) => {
    setMonChoix(e.target.dataset.id);
  };

  return (
    <PlayContainer>
      <TriangleBg
        src={process.env.PUBLIC_URL + "../images/bg-pentagon.svg"}
        alt="triangle"
      />
      <OptionsContainer onClick={handleChange}>
      <DivScissors>
          <Options
            data-id="scissors"
            onClick={setChoix}
            className="scissors"
            id="scissors"
          />
        </DivScissors>
        <DivPaper>
          <Options
            data-id="paper"
            onClick={setChoix}
            className="paper"
            id="paper"
          />
        </DivPaper>
        <DivLizard>
          <Options
            data-id="lizard"
            onClick={setChoix}
            className="lizard"
            id="lizard"
          />
        </DivLizard>
        <DivSpock>
          <Options
            data-id="spock"
            onClick={setChoix}
            className="spock"
            id="spock"
          />
        </DivSpock>
        <DivRock>
          <Options
            data-id="rock"
            onClick={setChoix}
            className="rock"
            id="rock"
          />
        </DivRock>
        
      </OptionsContainer>
    </PlayContainer>
  );
};

export default PlayLS;
